  font-family: "Work Sans", sans-serif;
  --color-text-primary: #3a3b3c;
  --color-text-secondary: #7b7b7b;
  --color-background-primary: #fff;
  --color-background-secondary: #fff7f4;
  --color-background-default: #fff;
  --color-accent: #ff5300;
  --color-accent-hover: rgb(255, 83, 0, 0.5);
  background-color: var(--color-background-default); }

.dark-theme {
  --color-text-primary: #fff;
  --color-text-secondary: #7b7b7b;
  --color-background-primary: #3a3b3c;
  --color-background-secondary: #242526;
  --color-background-default: #18191a;
  --color-accent: #ff5300; }

h1, h2, h3, h4 {
  color: var(--color-text-primary); }

ul, li {
