@import "../variables/variables.scss";
@import "../breakpoints/breakpoints.scss";
@import "../mixins/mixins.scss";

@include media("<=tablet") {
  .about-section {
    background-color: var(--color-background-default);
    padding: size(50) size(16);

    .about-image {
      width: 100%;
      max-width: size(684);
      max-height: size(352);
      margin-bottom: size(50);
    }

    .about-content {
      text-align-last: center;

      .section-little-title {
        margin-bottom: size(18);
      }

      .about-title {
        font-size: $fz-title-ipad-mini;
        font-weight: 500;
        text-align: center;
        margin-bottom: size(18);
      }

      .about-text {
        text-align: center;
        font-size: $font-size-paragraph-text;
        display: block;
        margin-bottom: size(24);
        color: var(--color-text-primary);
        line-height: (1.6);
      }

      .common-button {
        margin-bottom: size(50);
      }
    }

    .about-list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .about-item {
        padding: size(25) size(20) 0;
        display: inline-block;
        text-align: center;
        background-color: var(--color-background-primary);
        width: 100%;
        max-width: size(342);
        height: size(200);
        margin-bottom: size(24);
        box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);

        .about-item-button {
          width: size(70);
          height: size(70);
          margin-bottom: size(36);
          background-color: #7B7B7B;
          border: 0;
          border-radius: 50%;
        }

        .about-item-text {
          display: block;
          font-size: $fs-item-phone;
          color: var(--color-text-primary);
        }
      }
    }
  }
}

