@import "../variables/variables.scss";
@import "../breakpoints/breakpoints.scss";
@import "../mixins/mixins.scss";

@include media("<=phone") {
  .blog-section {
    .blog-content {
      padding: size(120) 0;

      .section-little-title {
        margin-bottom: size(18);
        text-align: center;
      }

      .blog-title {
        font-size: $fz-title-ipad-mini;
        margin-bottom: size(18);
        text-align: center;
      }

      .blog-slider {
        .blog-slider-list {
          overflow: hidden;

          .slick-track {
            display: flex;
            justify-content: space-between;
            padding: size(10) 0;

            .blog-slider-item {
              width: 100%;
              max-width: size(300);
              height: auto;
              margin: 0 auto;
              background-color: var(--color-background-primary);
              box-shadow: 0 0 size(10) 0 rgba(34, 60, 80, 0.2);
              padding: size(18);

              .blog-item-content {
                display: inline-block;
                vertical-align: top;
                width: 60%;

                .blog-item-title {
                  font-size: $fs-title-phone;
                  font-weight: 400;
                  color: var(--color-accent);
                  margin-bottom: size(18);
                }

                .blog-item-text {
                  font-size: $font-size-paragraph-text;
                  font-weight: 500;
                  margin: 0 0 size(20);
                  color: var(--color-text-primary);
                }
              }

              .blog-item-image {
                display: inline-block;
                float: right;
                max-width: size(80);
                margin-top: size(12);
                width: 40%;
                height: auto;
              }

              .blog-item-person {
                display: flex;
                align-items: center;

                .blog-person-photo {
                  display: inline-block;
                  width: size(40);
                  height: size(40);
                  margin-right: size(10);
                }

                .blog-person-data {
                  display: inline-block;

                  .blog-person-name {
                    font-size: $font-size-paragraph-text;
                    font-weight: 500;
                    display: block;
                    margin-bottom: size(6);
                    color: var(--color-text-primary);
                  }

                  .date-person-post {
                    display: inline-block;
                    font-size: $fs-title-phone;
                    color: var(--color-text-secondary);
                    margin-right: size(6);
                    font-weight: 400;
                  }

                  .time-to-read {
                    display: inline-block;
                    font-size: $fs-title-phone;
                    color: var(--color-text-secondary);
                    font-weight: 400;

                    &::before {
                      content: "";
                      display: inline-block;
                      width: size(5);
                      margin-right: size(10);
                      border-radius: 50%;
                      margin-bottom: size(3);
                      height: size(5);
                      background-color: var(--color-text-secondary);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

